import './App.css';

import { createMuiTheme, ThemeProvider } from "@material-ui/core";
import RootRouter from "./routing/root-router";


const theme = createMuiTheme({
  typography: {
    fontFamily: ["Poppins", "sans-serif"].join(","),
  },
});

function App() {
  return (
      <ThemeProvider theme={theme}>
        <RootRouter />
      </ThemeProvider>
  );
}

export default App;